<svelte:head>
	<title>Blog</title>
</svelte:head>

<h1>Recent posts</h1>

<ul>
	{#each posts as post}
		<li><a href="/blog/{post.slug}">{post.title}</a></li>
	{/each}
</ul>

<script context="module">
	let posts = [];
	export function preload() {
		// return fetch('https://sapper-template.now.sh/blog.json')
		return fetch('http://localhost:5000/blog.json')
			.then(r => {r.json();console.log(r)})
			.then(arr => {
				console.log(arr)
				posts = arr;
			});
	}
</script>

<style>
	ul {
		margin: 0 0 1em 0;
		line-height: 1.5;
	}
</style>
